import { styled } from 'styled-components';

import Icon from '#components/Icon';

import * as device from '#theme/devices';

export const StyledSpecList = styled.ul`
  padding: 0;
  margin: 0;
  font: ${({ theme }) => theme.textStyle.specsList.small};
  list-style: none;

  @media ${device.laptop} {
    font: ${({ theme }) => theme.textStyle.specsList.base};
  }
`;

export const StyledSpecItem = styled.li`
  padding: ${({ theme }) => theme.spacing.x1}px 0;
  border-bottom: 1px solid ${(props) => props.theme.color.divider.black};
  font: inherit;
`;

export const StyledSpecItemWithSublist = styled(StyledSpecItem)`
  padding-top: ${({ theme }) => theme.spacing.x2}px;
`;

export const StyledSubSpecList = styled(StyledSpecList)``;

export const StyledSubSpecItem = styled.li`
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  padding: ${({ theme }) => theme.spacing.x1}px 0
    ${({ theme }) => theme.spacing.x0_5}px;
  font: inherit;
`;

export const StyledIcon = styled(Icon)`
  flex: 0 0 auto;
  margin: 0 ${({ theme }) => theme.spacing.x2}px 0 0;

  @media ${device.laptop} {
    margin: ${({ theme }) => theme.spacing.x0_5}px
      ${({ theme }) => theme.spacing.x2}px 0 0;
  }
`;
